ológico que conlleva por la naturaleza de reproducción de los seres patógenos que pueden estar en este medio acuoso. ")])]),_c('p',{staticClass:"mt-4",attrs:{"data-aos":"fade-up"}},[_vm._v("Para ello, existen diferentes métodos de control entre los que se encuentran la evaporación, estabilización, recirculación, procesos biológicos, procesos fisicoquímicos y tecnologías de membranas.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mt-4"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0",staticStyle:{"place-self":"center"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/23.png"),"data-aos":"fade-right"}})])]),_c('div',{staticClass:"col-lg-8",staticStyle:{"place-self":"center"}},[_c('p',{attrs:{"data-aos":"fade-up"}},[_vm._v("Para poder hacer una operación adecuada se debe contar con los recursos necesarios de acuerdo con la cantidad de residuos sólidos que se disponen en el relleno sanitario, teniendo en cuenta los recursos técnicos y humanos donde existe personal calificado (jefe del relleno sanitario, jefe de laboratorios, técnicos químicos, técnicos del relleno) y el no calificado (choferes de vehículos, portería y labores varias).")]),_c('div',{staticClass:"cajon cajon1 color-primario p-4 mt-4",staticStyle:{"background-color":"#F6F6F6"}},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-up"}},[_vm._v("Las instrucciones y medidas que permitan disponer y almacenar los residuos sólidos, con técnicas sanitarias idóneas, en sitios que reúnan las condiciones físicas y de adecuación, donde la peligrosidad y biodegradabilidad no originen riesgos para la salud de la comunidad ni deterioro del medio ambiente y de los recursos naturales deben incluir los siguientes componentes:")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"mt-5",attrs:{"data-aos":"fade-up"}},[_vm._v("Adicionalmente, a los compactadores se pueden opera